import React from 'react';
import './GetInTouchForm.scss'
import {Button, TextField} from "@mui/material";

const GetInTouchForm = () => {



    return (
        <div className='get-in'>
            <TextField
                label="'"
                // value={field.name}
                // error = {validObj.name}
                name='name'
                // helperText="Some important text"
                placeholder="  '"
                variant='outlined'
                // onChange={fillField}
            />
            <TextField
                label=""
                // value={field.name}
                // error = {validObj.name}
                name='tel'
                // helperText="Some important text"
                placeholder="  "
                variant='outlined'
                // onChange={fillField}
            />
            <textarea name='text' placeholder=''></textarea>
            <Button
                fullWidth
                sx={{gridColumn: '-1/1'}}
                variant='outlined'></Button>
        </div>
    );
};

export default GetInTouchForm;